@import url('https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700,800,900&display=swap');

*
{
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

html, body {
  overflow: hidden;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.tile {
  display: flex;
  max-width: 75px;
  max-height: 75px;
  width: 3.5vw;
  height: 3.5vw;
  border: 3px solid #d3d6da;
  margin: 5px;
  text-align: center;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  font-weight: bold;
  flex-direction: column;
  text-transform: uppercase;
  transition: all 0.3s ease-out;
}


.tiles {
  display: flex;
  flex-direction: row;
}

.tilegrid {
  margin-top: 1vh;
}

.test {
  width: fit-content;
  height: fit-content;
}

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  caret-color: transparent;
}

.key {
  max-width: 50px;
  max-height: 60px;
  width: 7vw;
  height: 12vw;
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
  user-select: none;
  background-color: #d3d6da;
  color: #000000;
  text-transform: uppercase;
  border: 0;
  padding: 0;
  margin: 0px 8px 12px 0px;
  font-size: 16px;
}

svg {
  transform: scale(2);
}

.keyboard {
  position: absolute;
  margin-top: 5vh;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
  
}

.startbutton {
  font-size: 1rem;
  padding: 0.5vw 1.5vh 0.5vw 1.5vh;
  border-radius: 8px;
  border: none;
  outline: none;
  background-color: whitesmoke;
  border: 1px solid #E2E8F0;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  cursor: pointer;
  user-select: none;
  transition: all 0.5s ease-out;
}

.slider {
  width: 50vw;
  max-width: 300px;
  height: 1vh;
  max-height: 10px;
  outline: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: cornflowerblue;
  border-radius: 10px;
  margin-top: 20px;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  max-width: 30px;
  max-height: 30px;
  width: 6vw;
  height: 6vw;
  background-color:slategray;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.slider::-moz-range-thumb {
  max-width: 30px;
  max-height: 30px;
  width: 6vw;
  height: 6vw;
  background-color: slategray;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.title {
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.logo {
  display: inline;
  width: 5%;
  height: 5%;
  vertical-align: middle;
}

h1 {
  margin-top: 10px;
  /*
  display: inline-block;
  vertical-align: middle;
  white-space: normal;
  */
}

.stats {
  position: absolute;
  right: 5px;
  bottom: 50%;
  border-radius: 10%;
  padding: 5px;
  box-shadow: 0 2px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transition: all 0.5s ease-in;
  overflow-x: hidden;
}

.statscontainer {
  list-style: none;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.togglestats {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
}

@media only screen and (max-width: 500px) {
  .key {
    margin: 0px 5px 9px 0px;
    font-size: 12px;
  }

  .tile {
    border: 2px solid #d3d6da;
    font-size: 0.8rem;
    width: 5vw;
    height: 5vw;
  }

  .tilegrid {
    margin-top: 5vh;
  }

  .startbutton {
    font-size: 1rem;
    padding: 5px 15px 5px 15px;
  }
  
  .stats {
    font-size: 8px;
    border-radius: 10%;
    /*
    padding: 5px;
    margin: 0;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin-top: 5px;
    */
  }
}
